<template>
  <div v-if="qna" class="regist">
    <h1 class="underline">QnA 정보</h1>
    <div class="regist_form">
      <label for="isbn">번호</label>
      <div class="view">{{qna.isbn}}</div>
      <label for="title">제목</label>
      <div class="view">{{qna.title}}</div>
      <label for="author">글쓴이</label>
      <div class="view">{{qna.author}}</div>
      <label for="time">작성날짜</label>
      <div class="view">{{qna.time}}</div>
      <label for="content">설명</label>
      <div class="view" v-html="enterToBr(qna.content)"></div>
      <div style="padding-top: 15px">
        <router-link :to="`/qna/modify/${qna.isbn}`" class="btn">수정</router-link>
        <a href="#" class="btn" @click="deleteQna">삭제</a>
        <router-link to="/qna" class="btn">목록</router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import http from "@/util/http-common";

export default {
  computed: {
    ...mapGetters(["qna"])
  },
  methods: {
    deleteQna(){
      if(confirm("삭제할까요?")){
        http.delete(`/qnaapi/${this.$route.params.isbn}`).then(({ data }) => {
          let msg = "삭제 처리시 문제가 발생했습니다.";
          if (data === "success") {
            msg = "삭제가 완료되었습니다.";
          }
          alert(msg);
          this.$router.push("/qna");
        });
      }
    },
    numberWithCommas(x) {
      return String(x).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    enterToBr(str) {
      return String(str).replace(/(?:\r\n|\r|\n)/g, "<br />");
    }
  }
};
</script>

